.chat_window {
    width: calc(100% - 20px);
    max-width: 380px;
    height: 100%;
    border-radius: 50px;
    margin: 15px auto;
    left: 50%;
    top: 30%;
    transform: translateX(120%) translateY(-10%);
    box-shadow: 0 50px 10px rgba(0, 0, 0, 0.15);
    background-color: black;
    overflow: hidden;
}