.top_menu {
    background-color: blue;
    width: 100%;
    padding: 15px 0 5px;

}
.top_menu .buttons {
    margin: 3px 0 0 20px;
    position: absolute;
}
.top_menu .buttons .button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    display: inline-block;
    margin-right: 5px;
    position: relative;
    cursor: pointer;
}
.top_menu .buttons .button.maximize {
    background-color: green;
}
.top_menu .buttons .button.minimize {
    background-color: red;
}

.top_menu .title {
    text-align: center;
    color: white;
    font-size: 25px;
}
