.bottom_wrapper {
    position: relative;
    width: 100%;
    background-color: transparent;
    padding: 20px 20px;
    bottom: 0;
}
.bottom_wrapper .message_input_wrapper {
    display: inline-block;
    height: 50px;
    border-radius: 50px;
    background-color: white;
    width: calc(100% - -10px);
    

    padding: 0 20px;
}
.bottom_wrapper .message_input_wrapper .message_input {
    border: none;
    height: 100%;
    width: calc(100% - 40px);
    position: relative;
    color: gray;
    overflow-y: auto;
    
}
.bottom_wrapper .send_message {
    width: 100px;
    height: 50px;
    display: inline-block;
    border-radius: 50px;
    background-color: green;
    border: 2px solid green;
    margin-top: 10px;
    margin-right: 5px;
    color: white;
    cursor: pointer;
    transition: all 0.2s linear;
    text-align: center;
    float: right;
    font-style: oblique
    ;
    font-weight: bold;
    
}
.bottom_wrapper .send_message:hover {
    color: green;
    background-color: darkgreen;
}
.bottom_wrapper .send_message .text {
    font-size: 18px;
    font-weight: 300;
    display: inline-block;
    line-height: 48px;
}

.message_template {
    display: none;
}